<template>
  <div class="login" @keydown.enter="handleSubmit">
    <div class="login-con">
      <el-card :bordered="false">
        <p slot="title">
          <el-icon type="log-in"></el-icon>
          欢迎登录
        </p>
        <div class="form-con">
          <el-form ref="loginForm" :model="form" :rules="rules">
            <el-form-item prop="userName">
              <el-input v-model="form.userName" placeholder="请输入用户名">
              <span slot="prepend">
                <el-icon  type="person" class="el-icon-message"> </el-icon>
              </span>
              </el-input>
            </el-form-item>
            <el-form-item prop="password">
              <el-input type="password" v-model="form.password" placeholder="请输入密码">
              <span slot="prepend">
                <el-icon  type="locked" class="el-icon-star-on"> </el-icon>
              </span>
              </el-input>
            </el-form-item>
            <el-form-item>
              <el-button class="btn" @click="handleSubmit" type="primary" long>登录</el-button>
            </el-form-item>
          </el-form>
        </div>
      </el-card>
    </div>
  </div>
</template>

<script>
    import ElCard from "element-ui/packages/card/src/main";
    import axios from 'axios';
    import Cookies from 'js-cookie';
    export default {
      components: {ElCard},
      name: "login",
        data() {
          return {
            form: {
              userName: '',
              password: ''
            },
            rules: {
              userName: [
                { required: true, message: '账号不能为空', trigger: 'blur' }
              ],
              password: [
                { required: true, message: '密码不能为空', trigger: 'blur' }
              ]
            },
            data: {},
            /* 定义登录账号密码出错 */
            error: false
          };
        },
      methods:{
        handleSubmit () {
              axios.post('/users/login', {
                username: this.form.userName,
                password: this.form.password
              }).then((response) => {
                let res = response.data;
                if (res.status === '0') {
                  this.access = res.result.access;
                  // Cookies.set('access', res.result.access);
                  this.data = res;
                  // 不要cookie 个人信息的啊
                  // Cookies.set('data', JSON.stringify(this.data));
                  Cookies.set('user', this.form.userName);
                  // localStorage.setItem('password', `${this.form.password}`);
                  // Cookies.set('password', this.form.password);
                  /*
                  *  进入home 页面路由 带参数 data 也就是个人信息过去
                  * */

                  this.$router.push({
                    name: 'home',
                    params: {
                      // access: res.result.access,
                      userId: res.result.userId
                    }
                  });
                }else {
                  alert("账号名或者密码出错");
                  return;
                }
              });
              // if (this.form.userName === 'iview_admin') {
              //     localStorage.setItem('access', '0');
              //     Cookies.set('access', 0);
              // } else {
              //     localStorage.setItem('access', '1');
              //     Cookies.set('access', 1);
              // }
              /*
              *  登录页面应当传参数过去 比如登录用户名的部门 和 权限
              * */
              console.log( "登录。。");
            }
      }
    }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .login
    width 100vw
    height 100vh
    background-image url('https://file.iviewui.com/iview-admin/login_bg.jpg')
    background-size cover
    background-position center
    position relative
    &-con
      position: absolute;
      right: 50px;
      top: 50%;
      transform: translateY(-60%);
      width: 300px;
      &-header
        font-size: 16px;
        font-weight: 300;
        text-align: center;
        padding: 30px 0;
      .form-con
        padding: 10px 0 0;
        .btn
          width 100%

</style>



